<template>
  <div class="game-screen">
    <div class="color-code-row">
      <div v-for="color in secretCode" :key="color.id" class="color-code-peg" :class="color.color"></div>
    </div>

    <div v-for="row in board" :key="row.id" class="code-peg-row" :style="{ 'z-index': row.id }">
      <div v-for="slot in row.slots" :key="slot.id">
        <div v-if="row === currentRow" class="code-peg-slot" @mouseover="showColorPickerOnHover($event, slot)"
          @mouseout="showColorPicker = false">
          <div :class="['code-peg', slot.color]"></div>
        </div>
        <div v-else class="code-peg-slot">
          <div :class="['code-peg', slot.color]"></div>
        </div>
      </div>
      <div class="key-pegs">
        <div v-for="n in row.blackPegs" :key="`black-peg-${n}`" class="key-peg black"></div>
        <div v-for="n in row.whitePegs" :key="`white-peg-${n}`" class="key-peg white"></div>
      </div>
    </div>
    <div class="color-palette">
      <div v-for="color in colors" :key="color" class="color" :class="color" @click="selectColor(color)"></div>
    </div>
    <button @click="submitGuess">Submit</button>
    
    {{ pickerPosition }}

    <div class="color-picker-container" v-if="true" :style="{ top: pickerPosition.y + 'px', left: pickerPosition.x + 'px' }">
      <select v-model="selectedColor">
        <option v-for="color in colors" :key="color" :value="color">{{ color }}</option>
      </select>
      <button @click="updateColor">Select</button>
    </div>

  </div>
</template>

<script>
export default {
  props: [
    'userNumberOfAttempts'
  ],

  data() {
    return {
      secretCode: [
        { id: 1, color: null },
        { id: 2, color: null },
        { id: 3, color: null },
        { id: 4, color: null }
      ],
      colors: ['red', 'blue', 'green', 'yellow', 'orange', 'purple'],
      board: [],
      currentRow: null,

      showColorPicker: false, // Control the visibility of the color picker
      selectedSlot: null,    // Store the selected slot for color picking
      selectedColor: '',     // Store the selected color
      pickerPosition: { x: 0, y: 0 }, //
    };
  },

  created() {
    this.initializeBoard();
    this.generateSecretCode();
  },

  methods: {
    selectColor(color) {
      if (this.currentRow) {
        const emptySlot = this.currentRow.slots.find(slot => slot.color === null);
        if (emptySlot)
          emptySlot.color = color;
      }

    },
    // Method to show the color picker when hovering over a slot
    showColorPickerOnHover(event, slot) {
      this.selectedSlot = slot;

      // Calculate the position based on the event's coordinates
      const rect = event.target.getBoundingClientRect();
      const x = rect.left + window.scrollX;
      const y = rect.top + window.scrollY - 40; // Adjust for the height of the color picker container

      console.log(this.pickerPosition)

      // Set the pickerPosition based on the calculated x and y
      this.pickerPosition = { x, y };

      this.showColorPicker = true;
      this.selectedColor = slot.color || this.colors[0];
    },

    // Method to update the color when a color is selected
    updateColor() {
      if (this.selectedSlot) {
        this.selectedSlot.color = this.selectedColor;
        this.showColorPicker = false;
      }
    },
    submitGuess() {
      if (this.currentRow) {
        // Check if all slots in the current row have been filled with colors
        const isRowFilled = this.currentRow.slots.every(slot => slot.color !== null);
        if (isRowFilled) {
          this.board[this.currentRow.id - 1] = this.currentRow
          // Logic to submit the guess and emit the event
          const guess = this.currentRow.slots.map(slot => slot.color);
          let blackPegs = 0;
          let whitePegs = 0;
          const secretCodeCopy = JSON.parse(JSON.stringify(this.secretCode));

          // Calculate black pegs (correct color and position)
          for (let i = 0; i < secretCodeCopy.length; i++) {
            if (guess[i] === secretCodeCopy[i].color) {
              blackPegs++;
              // Set the matched slot color to null to avoid counting it for white pegs
              guess[i] = null;
              secretCodeCopy[i].color = null;
            }
          }

          // Calculate white pegs (correct color but wrong position)
          for (let i = 0; i < secretCodeCopy.length; i++) {
            if (guess[i] !== null && secretCodeCopy.some(slot => slot.color === guess[i])) {
              whitePegs++;
              // Set the matched slot color to null to avoid counting it multiple times
              const index = secretCodeCopy.findIndex(slot => slot.color === guess[i]);
              secretCodeCopy[index].color = null;
            }
          }

          // Set the black and white pegs in the current row
          this.currentRow.blackPegs = blackPegs;
          this.currentRow.whitePegs = whitePegs;

          // Check if the player has won
          console.log("blackPegs")
          console.log(blackPegs)
          console.log("whitePegs")
          console.log(whitePegs)
          console.log("this.secretCode")
          console.log(this.secretCode)
          if (blackPegs === secretCodeCopy.length) {
            alert("You got it!");
            this.$emit('submitGuess', true);
          } else {
            // Move to the next row
            this.currentRow = this.board.find(row => row.slots.some(slot => slot.color === null));
            if (!this.currentRow) {
              alert("You lost!");
              this.$emit('submitGuess', false);
            }
          }
        } else {
          // Logic to submit the guess and emit the event
          alert("Please fill in the slots in the current row")

        }
      }
    },
    initializeBoard() {
      const numberOfAttempts = this.userNumberOfAttempts; // You can change this as per your requirements

      for (let i = 0; i < numberOfAttempts; i++) {
        const row = {
          id: i + 1,
          slots: [
            { id: 1, color: null },
            { id: 2, color: null },
            { id: 3, color: null },
            { id: 4, color: null }
          ]
        };
        this.board.push(row);
      }

      this.currentRow = this.board.find(row => row.slots.some(slot => slot.color === null));
      console.log(this.secretCode)
    },
    generateSecretCode() {
      this.secretCode.forEach(element => {
        const rnd = Math.floor(Math.random() * 6);
        element.color = this.colors[rnd];
      });
    },
  }
};
</script>

<style scoped>
.game-screen {
  text-align: center;
  background-color: #b0d9ff;
  /* Light gray background color */
}

/* Style for the color code row */
.color-code-row {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  /* Half-transparent black background */
  padding: 10px;
  /* Add some padding to improve visibility */
}

.color-code-peg {
  width: 40px;
  height: 40px;
  border: 2px solid black;
  margin: 0 5px;
  background-color: rgba(0, 0, 0, 0.5);
}

.code-peg-row {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.code-peg-slot {
  width: 40px;
  height: 40px;
  border: 2px solid black;
  margin: 0 5px;
}

.code-peg {
  width: 100%;
  height: 100%;
}

.key-pegs {
  display: flex;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 2px solid orange;
  margin: 0 5px;
}

.key-peg {
  width: 10px;
  height: 10px;
  margin: 0 2px;
  border-radius: 50%;
  background-color: white;
  /* Set the background color of key pegs to white */
}

.black {
  background-color: black;
}

.white {
  background-color: white;
}

.color-palette {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.color {
  width: 30px;
  height: 30px;
  margin: 0 5px;
  cursor: pointer;
}

.red {
  background-color: red;
}

.blue {
  background-color: blue;
}

.green {
  background-color: green;
}

.yellow {
  background-color: yellow;
}

.orange {
  background-color: orange;
}

.purple {
  background-color: purple;
}

.color-picker-popup {
  position: absolute;
  z-index: 999;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  display: inline-block;
  margin-top: 5px;
}
</style>