<template>
  <div class="app">
    <StartScreen v-if="!gameStarted" @startGame="startGame" />
    <NewGameSetup v-if="!gameStarted" @startGame="startGame"/>
    <GameScreen v-if="gameStarted" @submitGuess="submitGuess" :userNumberOfAttempts="numberOfAttempts"/>
    <EndScreen v-if="gameEnded" :outcome="gameOutcome" @startNewGame="startNewGame" />
  </div>
</template>

<script>
import StartScreen from './components/StartScreen.vue';
import NewGameSetup from './components/NewGameSetup.vue';
import GameScreen from './components/GameScreen.vue';
import EndScreen from './components/EndScreen.vue';
export default {
  components: {
    StartScreen,
    NewGameSetup,
    GameScreen,
    EndScreen
  },

  data() {
    return {
      gameStarted: false,
      gameEnded: false,
      gameOutcome: null,
      numberOfAttempts: 10
    };
  },

  methods: {
    startGame(numberOfAttempts) {
      this.gameStarted = true;
      this.numberOfAttempts = numberOfAttempts;
    },
    submitGuess(gotit) {
      // Logic to submit the guess and process the feedback
      console.log(gotit)
      if (gotit) {
        this.gameEnded = true;
        this.gameOutcome = 'victory';
      } else {
        this.gameEnded = true;
        this.gameOutcome = 'defeat';
      } 
    },
    startNewGame() {
      this.gameStarted = false;
      this.gameEnded = false;
      this.gameOutcome = null;
    }
  }
};
</script>

<style scoped>
.app {
  text-align: center;
}
</style>