<template>
  <div class="start-screen">
    <h1>Mastermind</h1>
    <!-- <button @click="startGame">New Game</button> -->
    <button @click="showInstructions">Instructions</button>

    <!-- Add a modal popup for instructions -->
    <div>{{ showPopup }}</div>
    <div v-if="showPopup" class="modal">
      <div class="modal-content">
        <span class="close" @click="closeInstructions">&times;</span>
        <!-- Add your instructions content here -->
        <h2>How to Play Mastermind</h2>
        <p>Welcome to the Mastermind game! Here's how to play:</p>
        <ul class="instructions-list">
          <li>
            <strong>Objective:</strong> Your goal is to guess the secret color code.
          </li>
          <li>
            <strong>Color Code:</strong> The secret code consists of a sequence of four colored pegs.
          </li>
          <li>
            <strong>Color Choices:</strong> You have six different colors to choose from: Red, Blue, Green, Yellow,
            Orange, and Purple.
          </li>
          <li>
            <strong>Guessing:</strong> You will make a series of guesses to crack the code. Click on the color pegs to
            create your guess.
          </li>
          <li>
            <strong>Feedback:</strong> After each guess, you will receive feedback in the form of black and white pegs.
            <ul>
              <li><strong>Black Pegs:</strong> A black peg means you have a correct color in the correct position.</li>
              <li><strong>White Pegs:</strong> A white peg means you have a correct color but in the wrong position.</li>
            </ul>
          </li>
          <li>
            <strong>Winning:</strong> You win when you guess the correct code in 10 attempts or less. Good luck!
          </li>
          <li>
            <strong>Reset:</strong> To start a new game, click the "New Game" button.
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  data() {
    return {
      showPopup: false
    };
  },
  methods: {
    startGame() {
      this.$emit('startGame');
    },
    showInstructions() {
      console.log('showInstructions');
      // Show the instructions popup
      this.showPopup = true;
    },
    closeInstructions() {
      // Close the instructions popup
      this.showPopup = false;
    }
  }
}
</script>

