<template>
  <div class="end-screen">
    <h2>{{ outcome }}</h2>
    <button @click="startNewGame">New Game</button>
  </div>
</template>

<script>
export default {
  props: {
    outcome: {
      type: String,
      required: true
    }
  },
  methods: {
    startNewGame() {
      this.$emit('startNewGame');
    }
  }
}
</script>

<style scoped>
.end-screen {
  text-align: center;
}
</style>