<template>
    <div class="new-game-setup">
      <h2>New Game Setup</h2>
      <div>
        <label for="attempts">Number of Attempts:</label>
        <input type="number" id="attempts" v-model="numberOfAttempts">
      </div>
      <div>
        <label for="difficulty">Difficulty Level:</label>
        <select id="difficulty" v-model="difficultyLevel">
          <option value="easy">Easy</option>
          <option value="medium">Medium</option>
          <option value="hard">Hard</option>
        </select>
      </div>
      <button @click="startGame">Start Game</button>
    </div>
  </template>
  
  <script>
export default {
    data() {
      return {
        numberOfAttempts: 10,
        difficultyLevel: 'medium'
      };
    },
    methods: {
      startGame() {
        this.$emit('startGame', this.numberOfAttempts);
      }
    }
  }
</script>

<style scoped>
  .new-game-setup {
    text-align: center;
  }
  </style>